import {
  BoxBufferGeometry,
  BufferAttribute,
  BufferGeometry,
  Color,
  Line,
  LineBasicMaterial,
  Mesh,
  MeshBasicMaterial,
  Object3D,
  SphereBufferGeometry,
  Vector3,
} from 'three';

import { LineGeometry } from './LineGeometry.js'
import { Line2 } from './Line2.js'
import { LineMaterial } from './LineMaterial.js'

const colors = {
  PLAYER: new Color( 0xee836f ).convertGammaToLinear( 2.2 ).getHex(),
  TARGET: new Color( 0xdccb18 ).convertGammaToLinear( 2.2 ).getHex(),
  PATH: new Color( 0x99b3ff ).convertGammaToLinear( 2.2 ).getHex(),
  WAYPOINT: new Color( 0x00a3af ).convertGammaToLinear( 2.2 ).getHex(),
  CLAMPED_STEP: new Color( 0xdcd3b2 ).convertGammaToLinear( 2.2 ).getHex(),
  CLOSEST_NODE: new Color( 0x43676b ).convertGammaToLinear( 2.2 ).getHex(),
};

const OFFSET = 0.2;

/**
 * Helper for debugging pathfinding behavior.
 */
class PathfindingHelper extends Object3D {
  constructor () {
    super();

    this._playerMarker = new Mesh(
      new SphereBufferGeometry( 1, 26, 26 ),
      new MeshBasicMaterial( { color: colors.PLAYER } )
    );

    this._targetMarker = new Mesh(
      new BoxBufferGeometry( 0.3, 0.3, 0.3 ),
      new MeshBasicMaterial( { color: colors.TARGET } )
    );


    this._nodeMarker = new Mesh(
      new BoxBufferGeometry( 0.1, 0.8, 0.1 ),
      new MeshBasicMaterial( { color: colors.CLOSEST_NODE } )
    );


    this._stepMarker = new Mesh(
      new BoxBufferGeometry( 0.1, 1, 0.1 ),
      new MeshBasicMaterial( { color: colors.CLAMPED_STEP } )
    );

    this._pathMarker = new Object3D();

    this._pathLineMaterial = new LineMaterial({
      color: colors.PATH,
      linewidth: 20,

    } ) ;
    this._pathPointMaterial = new MeshBasicMaterial( { color: colors.WAYPOINT } );
    this._pathPointGeometry = new SphereBufferGeometry( 0.08 );

    this._markers = [
      this._playerMarker,
      this._targetMarker,
      this._nodeMarker,
      this._stepMarker,
      this._pathMarker,
    ];

    this._markers.forEach( ( marker ) => {

      marker.visible = false;

      this.add( marker );

    } );

  }

  /**
   * @param {Array<Vector3>} path
   * @return {this}
   */
  setPath ( path ) {

    while ( this._pathMarker.children.length ) {

      this._pathMarker.children[ 0 ].visible = false;
      this._pathMarker.remove( this._pathMarker.children[ 0 ] );

    }

    path = [ this._playerMarker.position ].concat( path );

    // Draw debug lines
    //使用buffergeometry无法修改线段宽度，故使用网格
    const geometry = new LineGeometry();
    //geometry.setAttribute('position', new BufferAttribute(new Float32Array(path.length * 3), 3));
    let pointArr =[]
    for (let i = 0; i < path.length; i++) {
      pointArr.push(path[i].x);
      pointArr.push(path[i].y);
      pointArr.push(path[i].z);
    }
    geometry.setPositions(pointArr);
    this._pathLineMaterial.resolution.set(window.innerWidth,window.innerHeight);
    this._pathMarker.add( new Line2( geometry, this._pathLineMaterial ) );

    //显示拐点
    // for ( let i = 0; i < path.length - 1; i++ ) {
    //
    //   const node = new Mesh( this._pathPointGeometry, this._pathPointMaterial );
    //   node.position.copy( path[ i ] );
    //   node.position.y += OFFSET;
    //   this._pathMarker.add( node );
    //
    // }

    this._pathMarker.visible = true;

    return this;

  }

  /**
   * @param {Vector3} position
   * @return {this}
   */
  setPlayerPosition( position ) {

    this._playerMarker.position.copy( position );
    this._playerMarker.visible = true;
    return this;

  }

  /**
   * @param {Vector3} position
   * @return {this}
   */
  setTargetPosition( position ) {

    this._targetMarker.position.copy( position );
    this._targetMarker.visible = true;
    return this;

  }

  /**
   * @param {Vector3} position
   * @return {this}
   */
  setNodePosition( position ) {

    this._nodeMarker.position.copy( position );
    this._nodeMarker.visible = true;
    return this;

  }

  /**
   * @param {Vector3} position
   * @return {this}
   */
  setStepPosition( position ) {

    this._stepMarker.position.copy( position );
    this._stepMarker.visible = true;
    return this;

  }

  /**
   * Hides all markers.
   * @return {this}
   */
  reset () {

    while ( this._pathMarker.children.length ) {

      this._pathMarker.children[ 0 ].visible = false;
      this._pathMarker.remove( this._pathMarker.children[ 0 ] );

    }

    this._markers.forEach( ( marker ) => {

      marker.visible = false;

    } );

    return this;

  }

}

export { PathfindingHelper };
